<template>
  <div id="loading-container" class="vs-con-loading__container">
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <nv-card>
      <div class="nv-row mb-4">
        <div class="nv-col sm:w w-full">
          <label>{{ $t('FILE_TYPE') }}</label>
          <ul class="flex mt-2">
            <li><vs-radio v-model="fileType" class="mr-4" vs-value="BIC">{{ $t('BIC') }}</vs-radio></li>
            <li><vs-radio v-model="fileType" class="mr-4" vs-value="TEXT">{{ $t('TEXT') }}</vs-radio></li>
          </ul>
        </div>
      </div>

      <div class="nv-row mb-4">
        <div class="nv-col sm:w w-full required">
          <label>{{ $t('FILE') }}</label>
          <nv-input-group class="bg-white">
            <template slot="append">
              <div class="append-text bg-dark">
                <i class="far fa-folder"></i>
              </div>
            </template>
            <input class="vs-inputx vs-input--input normal" type="file" id="file" ref="file" v-on:change="handleFileUpload()"/>
          </nv-input-group>
        </div>
      </div>

      <template v-if="fileType === 'TEXT'">
        <div class="nv-row mb-4">
          <div class="nv-col sm:w w-full">
            <label class="flex">{{ $t('ROW_PATTERN') }}&nbsp;<nv-tooltip :text="$t('PATTERN_HELP', patternKeywords)"><i class="far fa-question-circle"></i></nv-tooltip></label>
            <vs-input class="w-full" v-model="rowPattern" :title="$t('PATTERN')"/>
          </div>
        </div>
        <div class="nv-row mb-4">
          <div class="nv-col w-full">
            <vs-checkbox v-model="skipFirstRow">{{ $t('SKIP_FIRST_ROW') }}</vs-checkbox>
          </div>
        </div>
      </template>

      <div class="nv-row">
        <div class="nv-col w-full">
          <div class="flex justify-between">
            <vs-button class="px-5 py-3" color="primary" type="border" @click="cancel">{{ $t('CANCEL') }}</vs-button>
            <vs-button type="filled" @click="importFile">{{ $t('SUBMIT') }}</vs-button>
          </div>
        </div>
      </div>
    </nv-card>
  </div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import BankService from '@/services/bank/BankService'

export default {
  name: 'bank-import',
  mixins: [AlertMixin, LoadingMixin],
  data: function () {
    return {
      fileType: 'BIC',
      file: null,
      rowPattern: '',
      skipFirstRow: false,
      patternKeywords: {
        bankName: '{bankName}',
        institutionCode: '{institutionCode}',
        branchCode: '{branchCode}',
        swiftCode: '{swiftCode}',
        countryName: '{countryName}',
        countryAlpha2Code: '{countryAlpha2Code}',
        countryAlpha3Code: '{countryAlpha3Code}',
        address1: '{address1}',
        address2: '{address2}',
        addressDistrict: '{addressDistrict}',
        addressCity: '{addressCity}',
        addressProvince: '{addressProvince}',
        addressPostalCode: '{addressPostalCode}',
        addressPoBox: '{addressPoBox}'
      }
    }
  },
  methods: {
    handleFileUpload () {
      this.file = this.$refs.file.files[0]
    },
    importBic () {
      let self = this
      let formData = new FormData()
      formData.append('directoryFile', this.file)
      this.startLoading('loading-container')
      BankService.importBic({
        formData: formData
      }).then(function (response) {
        self.stopLoading('loading-container')
        if (response.status === 200) {
          self.showSuccessAlert(response.data.messages)
        }
      }).catch(function (error) {
        self.stopLoading('loading-container')
        self.showDangerAlert(error.response.data.errors)
      })
    },
    importText () {
      let self = this
      let formData = new FormData()
      formData.append('textFile', this.file)
      formData.append('rowPattern', this.rowPattern)
      formData.append('skipFirstRow', this.skipFirstRow)
      this.startLoading('loading-container')
      BankService.importText({
        rowPattern: this.rowPattern,
        skipFirstRow: this.skipFirstRow ? 'Y' : 'N',
        formData: formData
      }).then(function (response) {
        self.stopLoading('loading-container')
        if (response.status === 200) {
          self.showSuccessAlert(response.data.messages)
        }
      }).catch(function (error) {
        self.stopLoading('loading-container')
        self.showDangerAlert(error.response.data.errors)
      })
    },
    importFile () {
      if (this.fileType === 'BIC') {
        this.importBic()
      } else if (this.fileType === 'TEXT') {
        this.importText()
      }
    },
    cancel () {
      this.$router.back()
    }
  }
}
</script>
